/**************************/
/* ROWS AND ROW MODIFIERS */
/**************************/
.row {
  width: 100%;
  margin: 0 auto;
  font-size: 0; }
  .row--contained {
    max-width: 1400px; }
  .row--flex {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    /* Auto width columns can make use of flex to help fill a gap or align content. */ }
    .row--flex .col--auto {
      display: flex;
      flex: 1; }
    .row--flex-left {
      justify-content: flex-start; }
    .row--flex-center {
      justify-content: center; }
    .row--flex-right {
      justify-content: flex-end; }

